@import "@/styles/_variables.scss";

    .scrollbar-container {
        display: flex;
        justify-content: center;
        height: 100%;
        width: 100px;
        padding: 100px 0;

        .scrollbar-track {
            position: relative;
            height: 100%;
            width: 2px;
            background-color: $primary;
        }
    
        .scrollbar-thumb {
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            height: 100px;
            width: 100px;
            background-color: $secondary;
            color: $primary;
            transition: top 0.75s ease-in;
        }
    }
