.text-primary{
  color:#CAA580
}
.scrollbar-container{
  display:flex;
  justify-content:center;
  height:100%;
  width:100px;
  padding:100px 0
}
.scrollbar-container .scrollbar-track{
  position:relative;
  height:100%;
  width:2px;
  background-color:#CAA580
}
.scrollbar-container .scrollbar-thumb{
  display:flex;
  align-items:center;
  justify-content:center;
  position:absolute;
  height:100px;
  width:100px;
  background-color:#2F2A35;
  color:#CAA580;
  transition:top 0.75s ease-in
}

.text-primary{
  color:#CAA580
}
body{
  background-color:#2F2A35;
  font-family:'Harmonia Sans', sans-serif !important
}
.triangle{
  position:absolute;
  right:0;
  bottom:0;
  width:0;
  height:0;
  border-top:75px solid transparent;
  border-right:100px solid #2F2A35;
  border-bottom:0 solid transparent;
  transition:0.5s ease-in-out
}
.triangle:hover{
  border-top:85px solid transparent;
  border-right:110px solid #2F2A35;
  cursor:pointer
}
.more-info{
  position:absolute;
  bottom:0;
  right:0;
  margin-bottom:20px;
  color:#CAA580;
  transform:rotate(-39.5deg);
  pointer-events:none
}
.close-button{
  display:flex;
  align-items:center;
  justify-content:center;
  top:60px;
  right:-50px;
  height:50px;
  width:50px;
  border:2px solid white;
  border-radius:50px
}
.close-button:hover{
  background-color:rgba(255,255,255,0.3)
}


